@import url(https://fonts.googleapis.com/css2?family=Gwendolyn:wght@700&display=swap);
body{
    background: rgb(36, 1, 68);
    color: white;
}



*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

* a, ul{
    text-decoration: none;
    list-style: none;
}

.nav-logo{
    font-family: 'Gwendolyn', cursive;
    font-size: 70px;
}

.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 4rem;
}


.nav ul{
    display: flex;
}

.nav ul li a{
    font-size: 25px;
    color: #fff;
    padding: 0 19px;
}

.nav ul li a:hover{
    color: aqua;
}

.nav ul li{
    padding: 6px 0;
}

.nav-toggle{
    display: none !important;
  height: 26px;
  width: 32px;
  position: absolute;
  top: 17px;
  right: 2rem;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.nav-toggle-t .nav-toggle1{
    /* position: absolute;
    left: 0;
    top: 11px; */
    transform: rotate(45deg) !important;
    transition: all 0.1s ease;
}
.nav-toggle1{
    position: absolute;
    transform-origin: 0% 0%;
    transform: rotate(0);
    transition: all 0.1s ease;
    margin-bottom: 5px;
}
.nav-toggle-t .nav-toggle2{
    width: 0%;
    transition: all .1s ease;
}
.nav-toggle2{
    position: absolute;
    top: 42%;
    overflow-x: hidden;
    width: 100%;
}
.nav-toggle-t .nav-toggle3{
    /* position: absolute;
    left: 0;
    top: 11px; */
    transform: rotate(-45deg) ;
    transition: all 0.1s ease;
}
.nav-toggle3{
    position: absolute;
    bottom: 0;
    transform-origin: 0% 100%;
    transform: rotate(0);
    transition: all 0.1s ease;
    margin-top: 5px;
}

.nav-toggle div{
    display: block;
  height: 4px;
  width: 100%;
  border-radius: 10px;
    background: white;
}


@media screen and (max-width:768px) {
    .navbar{
        padding: 0 2rem;
    }
    .nav-logo{
        font-size: 45px;
    }
    .nav-toggle{
        display: block !important;
    }

    .nav-nav{
        flex-direction: column;
        position: absolute;
        top: 54px;
        right: 30%;
        left: 0;
        text-align: center;
        width: 100%;
        z-index: 100;
        overflow-y: hidden;
        height: 0 ;
        transition: all .3s;
        background: rgba(94, 14, 241, 0.9);
    }
    .show{
        height: 180px !important;
    }
}




.homepage{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.homepage p{
    font-size: 45px
}

.h-btn{
    padding: 15px 20px;
    background: transparent;
    color: #fff;
    border: solid 1px #fff;
    border-radius: 10px;
    font-size: 19px;
    margin: 0 15px;
}
.h-btn:active{
    transform: scale(1.01);
}
.h-btn:hover{
    background: aqua;
}

.left-side{
    height: 600px;
    width: 600px;
    position: relative;
    padding-right: 2rem;
}

.left-side div{
    overflow-y: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}

.left-side div p{
    text-align: center;
    width: 500px;
}

.left-side p{
    position: absolute;
    top: 200px;
    color: #000;
    z-index: 15 !important;
}

.left-side-img{
    position: absolute;
    top: 0;
    left: 0;
    z-index: -10 !important;
    width: 100% !important;
    height: 100% !important;
}

.left-side-bottom{
    position: relative;
    bottom: 50px;
}






@media screen and (max-width:768px) {
    .left-side{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100% !important;
        height: 400px;
    }
    .left-side div{
        padding: 0 !important;
        text-align: center;
    }
    .left-side-img{
        width: 350px !important;
        height: 300px !important;
    }
    .left-side p{
        top: 100px !important;
        position: absolute;
        left: 8%;
        font-size: 27px;
        width: 280px !important;
    }
}
.about{
    display: flex;
    justify-content: space-around;
    padding: 0 4rem;
    padding-top: 3rem;
}
    
.about-left img{
    height: 40vh;
}

.about-right{
    padding: 2rem 4rem;
    width: 800px;
    color: aqua;
    animation: animate-color 4s 1;
    color: #fff;
    background: linear-gradient(to bottom right, blue, aqua);
    box-shadow: -5px -5px 30px 5px aqua, 5px 5px 30px 5px blue !important;
    
        transform:
          perspective(750px)
          translate3d(0px, 0px, -250px)
          rotateX(27deg)
          scale(0.9, 0.9);
        border-radius: 20px;
        
        box-shadow: 0 70px 40px -20px rgba(0, 0, 0, 0.2);
        transition: 0.4s ease-in-out transform;
}:hover {
    transform: translate3d(0px, 0px, -250px);
}

.about-right h2{
    text-align: center;
    font-weight: 500;
    font-size: 30px;
}
.about-right p{
    font-size: 30px;
}

@media screen and (max-width:768px) {
    .about{
        flex-direction: column;
        justify-content: center;
        padding: 0 0 !important;
    }
    .about-right{
        width: 90% !important;
        padding: 2rem 2rem;
        font-size: 16px;
    }
}
